import React, {useState} from 'react'
import {Button, Modal, Card, Image} from 'react-bootstrap'
import excursionThumb from '../../img/excursion-thumb.png'
import excursion from '../../img/excursion.png'
import {Link} from 'react-router-dom'

function ModalExcursion() {
    const [ModalExcursionShow, setModalExcursionShow] = useState(false);
  
    return (
      <>
        <img 
            className="p-4 flex-wrap project"
            onClick={() => setModalExcursionShow(true)}
            src={excursionThumb}
        />
        
        <Modal
          size="lg"
          show={ModalExcursionShow}
          onHide={() => setModalExcursionShow(false)}
          className="text-center"

        >
            <Modal.Header closeButton>
                <Modal.Title id="example-modal-sizes-title-sm">
                    Excursion
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Image src={excursion} fluid />
                <p>A promotional page for a fictional app called Excursion.</p>
                <Link to='/excursion'>
                    <Button>Have a look!</Button>
                </Link>
            </Modal.Body>
        </Modal>
      </>
    );
  }

  export default ModalExcursion