import React, {useState} from 'react'
import {Button, Modal, Card, Image} from 'react-bootstrap'
import ravenous from '../../img/ravenous2.jpg'
import {Link} from 'react-router-dom'


function ModalRavenous() {
    const [ModalRavenousShow, setModalRavenousShow] = useState(false);
  
    return (
      <>
        <img 
            className="p-4 flex-wrap project"
            onClick={() => setModalRavenousShow(true)}
            src={ravenous}
            fluid
        />
        
        <Modal
          size="lg"
          show={ModalRavenousShow}
          onHide={() => setModalRavenousShow(false)}
          aria-labelledby="example-modal-sizes-title-sm"
        >
            <Modal.Header closeButton>
                <Modal.Title id="example-modal-sizes-title-sm">
                    Ravenous
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className="text-center">
                <Image src={ravenous} fluid />
                <p>A clone of Yelp. A CodeCademy course project using the Yelp API. </p>
                <Link to='/ravenous'>
                    <Button>Have a look!</Button>
                </Link>
            </Modal.Body>
        </Modal>
      </>
    );
  }

  export default ModalRavenous