import React from 'react'
import './css/style.css'
// import './css/all.css'
import { FaCopyright } from 'react-icons/fa'



function Excursion() {
    const videoExcursion = 'https://s3.amazonaws.com/codecademy-content/programs/freelance-one/excursion/videos/excursion.mp4'
    return(
        <div className='excursion'>
            <h1>Discover hidden places in the world around you</h1>
            <h4><a href='#'>Download Excursion (Coming soon!)</a></h4>
            <video 
                autoPlay
                muted
                src={videoExcursion}
            />
            <h2>Your personal travel guide</h2>
            <h3>Excursion remembers places you like, and recommends new points of interest around you.</h3>
            <img src="https://s3.amazonaws.com/codecademy-content/programs/freelance-one/excursion/images/camp.jpg"/>
            <br />
            <img src="https://s3.amazonaws.com/codecademy-content/programs/freelance-one/excursion/images/phone.png"/>
            <h2>Coming Soon for iPhone and Android</h2>
            <h4>Download Ecursion (Coming Soon!)</h4>
            <footer>
            <p><FaCopyright /> Excursion</p>
            </footer>
        </div>
    )
}

export default Excursion