import React, {useState} from 'react'
import {Button, Modal, Card, Image} from 'react-bootstrap'
import colmarThumb from '../../img/colmaracademy-responsivewebpic-thumb.png'
import colmar from '../../img/colmaracademy-responsivewebpic.jpg'
import { Link } from 'react-router-dom';

function ModalColmarAcademy() {
    const [ModalColmarAcademyShow, setModalColmarAcademyShow] = useState(false);
  
    return (
      <>
        <img
            className="p-4 flex-wrap project"
            onClick={() => setModalColmarAcademyShow(true)}
            src={colmarThumb}
            fluid
        />
        
        <Modal
          size="lg"
          show={ModalColmarAcademyShow}
          onHide={() => setModalColmarAcademyShow(false)}
          aria-labelledby="example-modal-sizes-title-sm"
        >
            <Modal.Header closeButton>
                <Modal.Title id="example-modal-sizes-title-sm">
                    Colmar Academy
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className="text-center">
                <Image src={colmar} className="w-75"/>
                <p>A website I made during my course on CodeCademy. It features the homepage of the non existent Colmar Academy. Made using flexbox only.</p>
                <Link to='/colmarAcademy'>
                    <Button>Have a look!</Button>
                </Link>
            </Modal.Body>
        </Modal>
      </>
    );
  }

  export default ModalColmarAcademy