import React, {useState} from 'react'
import {Button, Modal, Card, Image} from 'react-bootstrap'
import jamming from '../../img/jamming2.jpg'
import {Link} from 'react-router-dom'

function ModalJamming() {
    const [Modal2Show, setModal2Show] = useState(false);
  
    return (
      <>
        <img
            className="p-4 project"
            onClick={() => setModal2Show(true)}
            src={jamming}
            fluid
        />        
        <Modal
          size="lg"
          show={Modal2Show}
          onHide={() => setModal2Show(false)}
          aria-labelledby="example-modal-sizes-title-sm"
        >
            <Modal.Header closeButton>
                <Modal.Title id="example-modal-sizes-title-sm">
                    Jamming
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className="text-center">
                <Image src={jamming} fluid />
                <p>
                    A project which enable Spotify users to create a playlist by accessing the Spotify library and save it to their account.
                    CodeCademy React course.
                </p>
                <Link to='/jamming' className="text-white">
                    <Button>Have a look!</Button>
                </Link>
            </Modal.Body>
        </Modal>
      </>
    );
  }

  export default ModalJamming