import React, {Component} from 'react'
import './Portfolio.scss'
import ScrollToNext from './Navigation/ScrollToNext'
import ScrollToPrevious from './Navigation/ScrollToPrevious'
import { Col } from 'react-bootstrap'


// import ModalMovieDB from './Modals/ModalMovieDB'
import ModalRavenous from './Modals/ModalRavenous'
import ModalFitActive from './Modals/ModalFitActive'
import ModalExcursion from './Modals/ModalExcursion'
import ModalColmarAcademy from './Modals/ModalColmarAcademy'
import './Modals/Modal.scss'
import ModalJamming from './Modals/ModalJamming'
import ModalFreelancer from './Modals/ModalFreelancerPortfolio'

class PortfolioPortage extends Component {

    render () {
        const {sectionOrder, positionOrder} = this.props;
        
        return(
            <section id="portfolio">
                <ScrollToPrevious
                    sectionOrder={sectionOrder}
                    positionOrder={positionOrder}
                />
                <div className="portfolioContent">
                    <h2 className="text-center pt-3 pb-0">Portfolio</h2>
                    <Col
                        xs={{
                            span:10,
                            offset:1,
                        }}
                    >
                    <div className="project-container align-items-center flex-wrap text-center">
                        {/* <Modal1 /> */}
                        <ModalJamming />
                        <ModalRavenous />
                        <ModalFreelancer />
                        <ModalFitActive />
                        <ModalExcursion />
                        <ModalColmarAcademy />
                    </div>
                    </Col>
                </div>
                <ScrollToNext
                    sectionOrder={sectionOrder}
                    positionOrder={positionOrder}
                />
            </section>
        )
    }

}

export default PortfolioPortage